import React, { Component } from "react";
import {images} from './data';

export class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            humanHand: null,
            comHand: null,
        };
        this.hands=["gu","choki","pa"]
    }

    pon =(val)=>{
        this.setState({humanHand:val,comHand:Math.floor(Math.random()*3)})
    }

    judge = () =>{
        if(this.state.comHand==null && this.state.humanHand==null){
            return;
        }
        const result = ((this.state.comHand-this.state.humanHand)+3) % 3
        if(result==0){
            return "DROW"
        }else if(result == 1){
            return "YOU WIN!!"
        }else if(result==2){
            return "YOU LOSE!!"
        }
    }

    render() {
        const { humanHand, comHand } = this.state;
        const hand_style={margin:30, backgroundColor:"#fff"}
        return (
            <div style={{textAlign:"center"}}>
                <h1>JANKEN GAME!!!</h1>
                <div >
                    <button style={hand_style} onClick={()=>this.pon(0)}><img width={100} height={100} src={images.hands[0]}/></button>
                    <button style={hand_style} onClick={()=>this.pon(1)}><img width={100} height={100} src={images.hands[1]}/></button>
                    <button style={hand_style} onClick={()=>this.pon(2)}><img width={100} height={100} src={images.hands[2]}/></button>
                </div>
                <div>
                    <div style={{display:"inline-block",margin:50}}>
                        <span>you</span><br/><img width={100} height={100} src={images.human}/>
                    </div>
                    <div style={{display:"inline-block",margin:50}}>
                        <img width={100} height={100} src={images.hands[humanHand]}/>
                    </div>
                    <div style={{display:"inline-block",margin:50}}>
                        <img width={100} height={100} src={images.hands[comHand]}/>
                    </div>
                    <div style={{display:"inline-block",margin:50}}>
                        <span>com</span><br/><img width={100} height={100} src={images.robot}/>
                    </div>
                </div>
                <h1>{this.judge()}</h1>
            </div>
        );
    }
}

export default App;
